    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0;
    margin: 0;
    background-color: aqua;
    width: 100%;
    position: fixed;
}

li{
    cursor: pointer;
}

@media only screen and (max-width: 500px) {
    ul{
        bottom: 0;
    }
